"use client";

import { useChat } from "@ai-sdk/react";

export default function TestChat() {
  const {
    messages,
    input,
    handleInputChange,
    handleSubmit,
    isLoading
  } = useChat({
    api: "/api/chat",
  });

  return (
    <div className="p-6 space-y-4 max-w-2xl mx-auto">
      <div className="h-80 overflow-y-scroll border p-3 rounded space-y-2">
        {messages.map((m) => (
          <div
            key={m.id}
            className={m.role === "user" ? "text-right" : "text-left bg-neutral-900 p-2 rounded"}
          >
            <b>{m.role === "user" ? "You" : "Second Brain"}:</b>
            {m.parts.map((p, i) => (
              <div key={i}>{p.text}</div>
            ))}
          </div>
        ))}
      </div>

      <form onSubmit={handleSubmit} className="flex gap-2">
        <input
          value={input}
          onChange={handleInputChange}
          className="flex-1 border px-3 py-2 rounded bg-black"
          placeholder="Ask something..."
        />
        <button type="submit" className="border px-4 py-2 rounded">
          {isLoading ? "..." : "Send"}
        </button>
      </form>
    </div>
  );
}
